import styles from './Produtos.module.css'
import logo from '../imgs/elaborativa.jpg'

import palestra from '../imgs/produtos/palestra2.jpg'
import workshop from '../imgs/produtos/workshop.jpg'
import design from '../imgs/produtos/designAprendizagem.jpg'
import formacao from '../imgs/produtos/formao2.jpg'

function Produtos() {
    return (
        <div>
            <div className={styles.tituloProdutos}>
                <h1 id='produtos' className={styles.h1Produtos}>PRODUTOS</h1>
                <img src={logo} id={styles.logoBranca} />
            </div>
            <div className={styles.divProduto}>
                <img src={palestra} className={styles.imgsProdutos} />
                <div className={styles.divText}>
                    <h2 className={styles.h2Produtos}>PALESTRAS TEMTICAS</h2>
                    <p className={styles.linhaProdutos}>a</p>
                    <p className={styles.textoProdutos}>A ELABORATIVA oferece palestras temticas e motivadoras baseadas nas Competncias para o Sculo XXI, enfocando as metodologias ativas, geis, imersivas e analticas de ensino, soft skills (competncias socioemocionais), realidade aumentada (RA) e realidade virtual (RV), atividade laboral, empreendedorismo e inovao educacional, criatividade,etc.</p>
                </div>
            </div>
            <div className={styles.divProdutoEsquerda}>
                <div className={styles.divText}>
                    <h2 className={styles.h2Produtos}>CAPACITAO / WORKSHOPS</h2>
                    <p className={styles.linhaProdutos}>a</p>
                    <p className={styles.textoProdutos}>Oferecemos capacitaes ao corpo docente de instituies de ensino que queiram conhecer, ampliar e se atualizar no uso das metodologias ativas de ensino, RA, RV, sempre trabalhando com a mo na massa aplicando cada uma delas para melhorar a sua performance em sala de aula e concentrar o foco no protagonismo do aluno, mantendo o profissional atualizado, motivado e ativo para as demandasdomercado.</p>
                </div>
                <img src={workshop} className={styles.imgsProdutos} id={styles.imgDireita} />
            </div>
            <div className={styles.divProduto}>
                <img src={design} className={styles.imgsProdutos} />
                <div className={styles.divText}>
                    <h2 className={styles.h2Produtos}>DESIGN DE APRENDIZAGEM</h2>
                    <p className={styles.linhaProdutos}>a</p>
                    <p className={styles.textoProdutos}>A ELABORATIVA oferece um servio inovador, baseado na neurocincia, de atendimento personalizado e customizado a alunos que necessitam de acompanhamento para melhor gerenciar seu processo de aprendizagem. Aplicamos testes de aprendizagem, traamos estratgias especficas, elaboramos horrio de estudo funcional, disponibilizamos feedback aos pais, dentre outras atividades socioemocionais para estes alunos, otimizando o tempo de estudo e melhorando as notas eosresultados.</p>
                </div>
            </div>
            <div className={styles.divProdutoEsquerda}>
                <div className={styles.divText}>
                    <h2 className={styles.h2Produtos}>FORMAO CONTINUADA</h2>
                    <p className={styles.linhaProdutos}>a</p>
                    <p className={styles.textoProdutos}>A Elaborativa oferece um servio de formao continuada inovador, combinando mtodos presenciais e on-line em encontros mensais. Seu programa abrange uma ampla gama de tpicos, incluindo Metodologias Ativas, Realidade Aumentada, Realidade Virtual, Neurocincia e Aprendizagem, bem como Empreendedorismo, proporcionando aos profissionais oportunidades de aprofundar seus conhecimentos em reas essenciais. A instituio busca apoiar o crescimento e o sucesso dos participantes, permitindo-lhes atualizar suas habilidades de forma flexvel e acessvel,  medida que avanam em suas carreiras.</p>
                </div>
                <img src={formacao} className={styles.imgsProdutos} id={styles.imgDireita} />
            </div>
        </div>
    )
}

export default Produtos